
.row1,.row2,.row3{
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.start{
    text-align: center;
}
.start button{
    margin-top: 3rem;
    padding: 0.8rem 3.5rem;
    border-radius: 3px;
    border: none;
    box-shadow: 2px 2px 6px black;
    cursor: pointer;
    background-color: blue;
    color: white;
    font-size: 1.7rem;
    font-family: Arial, Helvetica, sans-serif;
}
.notification{
    text-align: center;
    margin-bottom: 2rem;
}
.notification{
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bolder;
    font-size: 1.5rem;
    text-shadow: 2px 2px 8px black;
}